/*Custom Fonts*/
@font-face {
    font-family: Centra;
    src: url("./assets/fonts/CentraNo2-Bold.ttf");
    font-weight: 700;
}

@font-face {
    font-family: Centra;
    src: url("./assets/fonts/CentraNo2-Medium.ttf");
    font-weight: 500;
}

@font-face {
    font-family: Centra;
    src: url("./assets/fonts/CentraNo2-Book.ttf");
    font-weight: 400;
}

/*Default css*/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 75px;
    font-family: Centra, serif;
}

.App {
    text-align: center;
}

body {
    font-weight: 400;
    overflow-x: hidden;
    position: relative;
    background: linear-gradient(90.21deg, rgb(98, 109, 171) -5.91%, rgba(81, 245, 0, 0.74) 111.58%);
    color: #ffffff !important;
    font-family: 'Centra', sans-serif !important;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    line-height: normal;
}

