
.banner {
    margin: 200px 50px 0 100px;
    font-weight: 400;
    color: #fff;
    padding: 70px 34px;
    font-size: 18px;
    text-align: justify;
}

.position {
    font-weight: 800;
    font-size: 30px;
}

.position:hover {
    color: rgba(70, 239, 44, 0.76);
    cursor: default;
    transition: 0.4s ease-in-out;
}

.banner-text h1 {
    font-size: 62px;
}

.banner-text h1:hover {
    color: rgba(70, 239, 44, 0.76);
    cursor: default;
    transition: 0.4s ease-in-out;
}

.banner-text p {
    font-size: 20px;
    margin: 30px 0 0 0;
}

.banner-avatar {
    border-radius: 50%;
    border: 5px solid rgba(75, 77, 89, 0.66);
    width: 420px;
    height: 420px;
    margin-left: 200px;
    transition: all 150ms ease-in-out;
}

.banner-avatar:hover {
    transform: scale(1.1);
}

@media screen and (max-width:480px) {
    .banner{
        flex-direction: column;
        max-width: 450px;
        margin: 100px 51px -5px -2px;
        font-size: 10px;
        text-align: justify;
    }

    .banner-text p {
        font-size: 18px;
        margin: 30px 0 20px 0;
    }

    .banner-text h1 {
        font-size: 45px;
    }

    .position {
        font-weight: 700;
        font-size: 22px;
    }

    .banner-avatar {
        border: 3px solid rgba(75, 77, 89, 0.66);
        width: 320px;
        height: 320px;
        /*margin: 75px 14px -60px;*/
        display: block;
        margin: 0 auto;
    }

}